body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.modal .modal-dialog .modal-content {
  padding: 20px;
}

.modal .modal-dialog .modal-content p {
  margin-top: 20px;
  margin-bottom: 0;
}

.modal .modal-dialog .modal-content h4 {
  margin-bottom: 0;
}

ul.pokelist-container li .pokemon-container {
  padding-bottom: 20px;
}

ul.pokelist-container li .pokemon-container p {
  margin-bottom: 5px;
}

.modal-dialog.modal-sm form label {
  width: 100%;
}

.modal-dialog.modal-sm form label::after {
  content: "";
  display: block;
  clear: both;
}

.modal-dialog.modal-sm form input {
  float: right;
}

/* add form */

body {
  position: relative;
}

.add-new-form-container {
  position: fixed;
  top: 0;
  right: 0;
  padding: 10px;
}

.add-new-form-container input, .add-new-form-container textarea, .add-new-form-container button {
  width: 100%;
}

.add-new-form-container textarea {
  padding: 10px;
  border-radius: 6px;
}

.add-new-form-container button {
  margin-top: 15px;
}

/* search input */
div.search-input {
  width: 100%;
  padding: 20px;
  margin-bottom: 20px;
  background-color: #00ffaf;
}

div.search-input input {
  border-radius: 6px;
  width: 100%;
  padding: 10px;
  border: 1px solid #00ffaf;
}

div.search-input input:focus {
  outline: none;
  box-shadow: inset 0 0 5px rgb(85 196 166 / 84%);
  border: 1px solid #045d41;
}

/* buttons */

button.btn-primary {
  color: #084230;
  background-color: #00ffaf;
  border-color: #00cc8c;
}

button.btn-primary:hover {
  color: #084230;
  background-color: #20d39b;
  border-color: #0e996e;
}

button.btn-primary.focus, button.btn-primary:focus {
  color: #084230;
  background-color: #20d39b;
  border-color: #0e996e;
  box-shadow: 0 0 0 0.2rem rgb(20 125 93 / 50%);
}

button.btn-primary:not(:disabled):not(.disabled).active, button.btn-primary:not(:disabled):not(.disabled):active, .show>.button.btn-primary.dropdown-toggle {
  color: #084230;
  background-color: #20d39b;
  border-color: #0e996e;
}